<template>
<div class="home" :style="{background: bg_accent}">
  <div class="page center">
    <h1 class="tit">
      Soy Roberto Mesejo y me encanta<br>
      <em class="tit_serif love">{{loves[love_count]}}</em>
    </h1>
  </div>
  <div class="page">
    <section class="contexto" ref="bg_accent">
      <p class="bigparr yo">
        <b>Me fascina el proceso creativo.</b>
      </p>
      <p class="bigparr yo">
        <b>Intento conocer e involucrarme en varias de las etapas durante la creación de un producto</b> (para así tener una visión lo más completa posible).
      </p>
      <!-- <small class="parr yo">*Tengo experiencia trabajando en diferentes tipos de entornos y en varios tipos de productos. Por lo que también he catalogado los proyectos según para quién he realizado el proyecto (estudios, empresas, como freelance o emprendedor) y el producto final (plataformas, webs y apps)</small> -->
    </section>
    <section class="grid">
      <div class="lines grid grid-gap-md">
        <span class="child col-4"></span>
        <span class="child col-4"></span>
        <span class="child col-4"></span>
      </div>
      <p class="bigparr yo">
        <b>Así he organizado mis proyectos según la fase en la que he intervenido:</b>
      </p>
      <ul class="grid grid-gap-md filtros">
        <li class="child col-4">
          <p class="filtro bigparr yo" :class="{active: incluyeFiltro('product design')}" @click="filterProyects('product design')">product design</p>
          <p class="filtro bigparr yo" :class="{active: incluyeFiltro('UX')}" @click="filterProyects('UX')">UX</p>
        </li>
        <li class="child col-4">
          <p class="filtro bigparr yo" :class="{active: incluyeFiltro('branding')}" @click="filterProyects('branding')">branding</p>
          <p class="filtro bigparr yo" :class="{active: incluyeFiltro('UI')}" @click="filterProyects('UI')">UI</p>
        </li>
        <li class="child col-4">
          <p class="filtro bigparr yo" :class="{active: incluyeFiltro('front-end')}" @click="filterProyects('front-end')">Front-end</p>
          <p class="filtro bigparr yo" :class="{active: incluyeFiltro('full stack')}" @click="filterProyects('full stack')">Full stack</p>
        </li>
      </ul>
      <porfolio 
        :currentScrollPosition="currentScrollPosition"
        :filtros_activos="filtros_activos"
        @active_proy="active_proy = $event"
      />
    </section>
  </div>
</div>
</template>

<script>
import porfolio from "@/components/porfolio.vue";
export default {
  name: 'Home',
  components: {porfolio},
  props: {
    currentScrollPosition: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      active_proy: false,
      filtros_activos: ['product design', 'UX', 'branding', 'UI', 'front-end', 'full stack'],
      bg_accent: '',
      love: '',
      love_count: 0,
      loves: [
        "crear",
        "diseñar productos/servicios",
        "aprender cosas nuevas",
        "organizar ideas",
        "buscar rutas alternativas",
        "los design sprints",
        "prototipar ideas",
        "la innovación",
        "trabajar en equipo",
        "conceptos redondos",
        "experimentar con nuevas herramientas",
        "APRENDER",
        "darle una vuelta"
      ]
    }
  },
  computed: {
  },
  watch: {
    currentScrollPosition() {
      if (this.currentScrollPosition > (this.$refs.bg_accent.offsetTop)) {
        this.bg_accent = 'linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 16.24%), #FBF6F1';
      } else {
        this.bg_accent = '';
      }
    }
  },
  mounted: function() {
    let that = this;
    setInterval(() => {
      that.love_count < (that.loves.length - 1) ? that.love_count++ : that.love_count = 0;
    }, 3000);
  },
  methods: {
    incluyeFiltro(filtro) {
      return this.active_proy && this.active_proy.includes(filtro);
    },
    filterProyects(filtro_a_activar) {
      if (!this.filtros_activos.includes(filtro_a_activar)) this.filtros_activos.push(filtro_a_activar)
      else {this.filtros_activos.splice(this.filtros_activos.indexOf(filtro_a_activar))}
    }
  }
}
</script>
